@media only screen and (max-width: 1100px) {
    .sign-in input, button{
        width                    : 90%;
    }
    .sign-in .sign-in-flex label {
        width                    : 90%;
    }
    @keyframes effect-hover-btn {
        from {
          box-shadow             :  0 0 4px 1px#33FF33;
          width                  : 90%;
        }
        to {
          box-shadow             :  0 0 15px 1px#33FF33;
          width                  : 80%;
        }
    }
}
@media only screen and (max-width: 680px) {
    h1 {
        font-size                : 25px;
    }
    .sign-in input, button {
        width                    : 95%;
    }
    .sign-in .sign-in-flex label {
        width                    : 95%;
    }

      
}
@media only screen and (max-width: 530px) {
    h1 {
        font-size                : 20px;
    }
}
@media only screen and (max-width: 390px) {
    .infobar:first-child {
        font-size                : 12px;
    }
    .close-infobar {
        font-size                : 16px;
    }
    
    .feed-container {
        padding                  : 10px;
        max-width                : 100%;
        height                   : 100%;
        min-height               : 60vh;
    }
}